.uploadImageContainer {
  cursor: pointer;
  transition: 1s ease-out;
}
.uploadImage {
  width: 250px;
  height: 220px;
  transition: 1s ease-out;
}

.delete {
  position: relative;
  top: -50%;
  left: 0;
  font-size: 1.3rem;
  color: white;
  text-shadow: 1px 1px 2px black;
  opacity: 0;
  transition: 0.3s ease-out;
  margin: 0;
}

.uploadImageContainer:hover .uploadImage {
  background: black;
  mix-blend-mode: multiply;
  transition: 1s ease-out;
}
.uploadImageContainer:hover .delete {
  opacity: 1;
}

.dropzone {
  width: 240px;
  height: 240px;
  border: 1px solid grey;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  cursor: pointer;
  margin: 1rem auto;
}

.dropzoneContainer {
  display: flex;
  justify-content: space-between;
  overflow: hidden;
}

@media only screen and (max-width: 768px) {
  .dropzoneContainer {
    flex-direction: column;
  }
  .delete {
    opacity: 0.7;
  }
}
